.main-nav{
  position: absolute;
  top: var(--header-height);
  left: 0;
  width: var(--vw);
  height: auto;
  z-index: calc(var(--z-fixed) - 10);
  background: $body-bg;
  //border: 1px solid red;
  transition: transform .5s;
  transform: translateY(-100%);
  @include to(l){
    position: fixed;
    width: 100%;
    height: auto;
    top: var(--header-height);
    left: 0;
  }
  &.show{
    transform: translateY(0);
    //z-index: calc(var(--z-fixed) + 20);
  }
  &.fixed{
    @include from($bp){
      transform: translateY(0);
      position: fixed;
      top: 0;
      border-bottom: $border;
    }
  }
}
.main-menu{
  @include edMenu(l);
  text-transform: uppercase;
  border-top: $border;
  border-bottom: $border;
  position: relative;
  .fixed &::after{
    display: none;
  }
  @include from(l){
    border: none;
    max-width: $max-width;
    margin: 0 auto;
    &::after{
      content: '';
      display: block;
      position: absolute;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
      width: 75%;
      height: 1px;
      background: $color-border;
    }

  }
  li{
    border-bottom: $border;
    &:last-child{
      border: none;
    }
    @include from($bp){
      border: none;
    }
  }
  a{
    @include from(l){
      line-height: 4em;
    }
  }
}
