.main-footer{
  width: 100%;
  max-width: $max-width;
  height: var(--footer-height);
  margin-top: calc(var(--vw) * .05);
  margin-left: auto;
  margin-right: auto;
  position: relative;
  &::after{
    content: '';
    display: block;
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 75%;
    height: 1px;
    background: $color-border;
  }
  .social-network{
    @extend %lists-style;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    li{
      margin-right: 1.5em;
      &:last-child{
        margin-right: 0;
      }
    }
    a{
      --size: 2em;
      display: block;
      height: var(--size);
      width: var(--size);
      @include from(m){
        --size: 2.5em
      }
      @include from(l){
        --size: 3em;
      }
    }
  }
}
