.is-mobile{
  position: fixed;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  background: $body-bg;
  z-index: calc(var(--z-modal) + 1000);
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  img{
    width: 75px;
    margin-bottom: 2em;
  }
  p{
    font-size: 1.4em;
  }
  @media screen and (orientation: landscape){
    display: flex;
  }
}
