.main-header{
  //background: lime;
  position: fixed;
  top: 0;
  left: 0;
  width: var(--vw);
  //border: 1px solid red;
  background: $body-bg;
  height: var(--header-height);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: var(--z-fixed);
  border-bottom: $border;
  @include from($bp){
    position: absolute;
    border: none;
  }
  h1{
    text-indent: -10000%;
    display: none !important;
  }
  .logo-container{
    width: var(--logoWidth);
  }

  .toggle-menu{
    $width: calc(var(--vw) * .06);
    position: absolute;
    z-index: calc(var(--z-fixed) + 10);
    width: $width;
    height: auto;
    right: calc(var(--vw) * .05);
    cursor: pointer;
    @include from(l){
      max-width: 50px;
    }
    @include from(1420px){
      max-width: 55px;
    }
    img{
      width: 100%;
    }
  }
}

