.pAbsolute{
  position: absolute;
  display: none;
}
main{
  //width: calc(var(--vw) - (var(--margin) * 2));
  //margin-left: auto;
  //margin-right: auto;

}
.nomobile{
  @include to($bp){
    display: none;
  }
}
.section-title{
  font-size: 2em;
  margin-bottom: 1rem;
  @include from(m){
    font-size: 5vw;
    margin-bottom: .5em;

  }
}
.container {
  width: 100%;
  max-width: $max-width;
  margin: var(--gutter) auto;
  &.full{
    max-width: calc(var(--vw) - (var(--margin) * 2));
  }
}
.banner{
  width: var(--vw);
  height: auto;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  &.right {
    justify-content: flex-end;
  }
  &.left{
  }
  img{
    width: calc(var(--vw) - var(--margin));
    height: auto;
    object-position: center;
    object-fit: cover;
  }
  &.full{
    img{
      width: 100%;
    }
  }
}
.text-wrapper{
  margin-bottom: 1em;
  @include from($bp){
    margin-bottom: 0;
  }
}
