.Inicio{
  &__grid {
    @include from($bp) {
      display: grid;
      grid-template-columns: 55% 1fr;
      grid-column-gap: calc(var(--margin) * 2);
    }
  }
  &__text{
    @include from($bp){
      display: flex;
      flex-direction: column;
      justify-content: center;
      padding-right:  calc(var(--margin) * 1.6);
      p{
        font-size: 2vw;
      }
    }
  }
}
