.section-top-grid{
  display: grid;
  grid-template-columns: 45% 1fr;
  grid-column-gap: calc(var(--margin) * 2);
  p{
    font-size: 2vw;
  }
}
.section-top-text{
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.section-top-img{
  transform: translateX(var(--margin));
}
.formulario{
  margin-top: calc(var(--gutter)*3);
}
.center{
  text-align: center;
  margin: 0 auto;
}
.form{
  margin-top: var(--gutter);
  --size: 1.2em;
  padding: 0 1em;
  @include from($bp){
    padding: 0;

  }
  @include from(m){
    --size: 1.3em;
  }
  @include from(l){
    --size: 1.5vw;
  }
  font-size: var(--size);
  input, select, textarea, label{
    font-size: var(--size);
    font-family: $font-family;
    width: 100%;
    display: block;
  }
  input, select, label{
    line-height: calc(var(--size) * 1.7);
    height: calc(var(--size) * 1.7);
  }
  input, select, textarea{
    padding: 0 .5em;
    border: 1px solid;
    &.error{
      outline: 1px solid red;
    }
  }
  textarea{
    resize: none;
    padding: .5em;
    height: 5em;
  }
  label{
    margin-bottom: .5rem;
    font-weight: bold;
  }
  option{
    font-size: calc(var(--size) * .8);
    text-transform: capitalize;
  }
  .input-group{
    margin-bottom: var(--size);
    &:last-child{
      margin-bottom: 0;
    }
  }
}
.select{
  position: relative;
  & select{
    -moz-appearance: none;
    -webkit-appearance: none;
    appearance: none;
    background: transparent;
  }

  &::after{
    content: '';
    display: block;
    position: absolute;
    background: url(../img/arrow-down.png) center / cover no-repeat;
    height: 100%;
    width: calc(var(--size) * 1.5);
    top: 0;
    right: 0;
    z-index: -1;
    cursor: pointer;
  }
}
.form-footer{
  display: flex;
  justify-content: flex-end;
  button{
    border: 1px solid rgba(#000,.8);
    font-size: inherit;
    font-style: italic;
    line-height: 1.8em;
    padding: 0 .8em;
    background: transparent;
    transition: background .3s;
    cursor: pointer;
    &:hover{
      background: darken(#fff,5);
    }
  }
}
.box.textarea{
  display: flex;
  flex-direction: column;
}
.textCount{
  align-self: flex-end;
  margin-top: .3em;
  font-size: calc(var(--size) * .6);
}
